.movies .movie {
  width: 45%;
  background-color: #fff;
  margin-bottom: 70px;
  _display: flex;
  _align-items: flex-start;
  _justify-content: space-between;
  font-weight: 300;
  padding: 20px;
  border-radius: 5px;
  color: #adaeb9;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.movies .movie a {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  text-decoration: none;
  color: inherit;
}

.movie img {
  position: relative;
  top: -50px;
  max-width: 150px;
  width: 100%;
  margin-right: 30px;
  box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
    0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);
}

.movie .movie__title,
.movie .movie__year {
  margin: 0;
  font-weight: 300;
}

.movie .movie__title {
  margin-bottom: 5px;
  font-size: 24px;
  color: #2c2c2c;
}

.movie .movie__genres {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  margin: 5px 0px;
}

.movie__genres li,
.movie .movie__year {
  margin-right: 10px;
  font-size: 14px;
}

.movie .movie__summary {
  text-align: justify;
  line-height: 18px;
  /* overflow: hidden;
  text-overflow: ellipsis;
  height: 3.6em;
  width: 100%;
  display: -webkit-box;
  word-wrap: break-word;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical; */
}

.movie .movie__more {
  color: #adaeb9;
  justify-content: flex-end;
  text-decoration: none;
  align-items: flex-end;
  display: flex;
}

.container {
  height: 100%;
  display: flex;
  justify-content: center;
}

.loader {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 300;
}

.movies {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
  padding: 50px;
  padding-top: 119px;
  width: 80%;
}

.about__container {
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
  padding: 20px;
  border-radius: 5px;
  background-color: white;
  margin: 0 auto;
  margin-top: 100px;
  max-width: 25%;
  font-weight: 300;
}

.about__container span:first-child {
  font-size: 20px;
}

.about__container span:last-child {
  display: block;
  margin-top: 10px;
}

.nav {
  display: grid;
  grid-template-columns: 60.4% 5% 1fr;
  background-color: rgba(255, 255, 255, 0.7);
  padding: 11px 20px;
  border-radius: 0px;
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 1;
}

.nav a {
  text-decoration: none;
  color: #000;
  text-transform: uppercase;
  font-size: 12px;
  text-align: center;
  font-weight: 600;
  display: flex;
  align-items: center;
}
.nav a:first-child {
  justify-content: flex-end;
  margin-right: 29px;
}
.nav a:last-child {
  justify-content: center;
}

.nav input {
  background-color: transparent;
}

.nav a:not(:last-child) {
  margin-bottom: 0px;
}

.zNoeh {
  display: none;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  background-color: #eff3f7;
  height: 100%;
}

